import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormHelperText from "@material-ui/core/FormHelperText";
import Select from "@material-ui/core/Select";
import { useHistory } from "react-router-dom";
import SaveIcon from "@material-ui/icons/Save";
import { db } from "./firebase";
import { setDoc, doc, onSnapshot, deleteDoc } from "firebase/firestore";
import TextField from "@material-ui/core/TextField";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 200,
    marginLeft: 100,
    marginTop: 20,
  },
  selectEmpty: {
    marginTop: theme.spacing(10),
  },
  button: {
    margin: theme.spacing(1),
    marginTop: 50,
    marginLeft: 100,
  },
  text1: {
    "& .MuiTextField-root": {
      margin: theme.spacing(1),
      width: "30ch",
      marginTop: "2ch",
      marginLeft: "-1ch",
    },
  },
  text2: {
    "& .MuiTextField-root": {
      margin: theme.spacing(0),
      width: "120ch",
      marginLeft: "10ch",
    },
  },
  text3: {
    "& .MuiTextField-root": {
      margin: theme.spacing(0),
      width: "40ch",
      marginLeft: "20ch",
    },
  },
  text4: {
    "& .MuiTextField-root": {
      margin: theme.spacing(0),
      width: "30ch",
      marginLeft: "5ch",
    },
  },
}));
export default function GeneralTest() {
  const classes = useStyles();
  const [quarter, setquarter] = React.useState("");
  const history = useHistory();
  const [sclass, setsclass] = React.useState("");
  const [option1, setoption1] = React.useState("");
  const [option2, setoption2] = React.useState("");
  const [option3, setoption3] = React.useState("");
  const [option4, setoption4] = React.useState("");
  const [question, setquestion] = React.useState("");
  const [answer, setanswer] = React.useState("");
  const [add, setadd] = React.useState("");
  const [order, setorder] = React.useState("");
  const [questionnumber, setquestionnumber] = React.useState("");
  const [test_name, settest_name] = React.useState("");
  const [doclength, setdoclength] = React.useState("");

  const validation = (e) => {
    //e.preventDefault();

    if (
      question === "" ||
      answer === "" ||
      option1 === "" ||
      option2 === "" ||
      option3 === "" ||
      option4 === "" ||
      sclass === "" ||
      quarter === "" ||
      test_name === "" ||
      order === ""
    ) {
      alert("Complete The fields");
    } else if (questionnumber === "") {
      alert("No Data Found");
    } else {
      handlesubmit();
    }
  };

  const handleaddcontrol = (e) => {
    if (sclass === "" || quarter === "" || test_name === "" || order === "") {
      alert("Complete the fields");
    } else {
      setadd("true");

      onSnapshot(
        doc(db, "test", sclass, "quarter", quarter, "test", test_name),
        (doc) => {
          const qno = doc.get("qno");
          if (quarter === "first") {
            setdoclength("11");
          } else if (quarter === "second") {
            setdoclength("12");
          } else if (quarter === "third") {
            setdoclength("13");
          }

          if (qno) {
            setquestionnumber(qno);
          } else {
            setquestionnumber("100");
          }
        }
      );
    }
  };

  const handlesubmit = (e) => {
    var intqno = parseInt(questionnumber);
    intqno = intqno + 1;

    const datas = {
      question: question,
      a: option1,
      b: option2,
      c: option3,
      d: option4,
      answer: answer,
      qno: questionnumber,
    };

    setDoc(doc(db, "test", sclass, "quarter", quarter), {
      order: doclength,
      quarter: quarter,
    }).then(() => {
      setDoc(
        doc(
          db,
          "test",
          sclass,
          "quarter",
          quarter,
          "test",
          test_name,
          "test",
          questionnumber.toString()
        ),
        datas
      ).then(() => {
        setDoc(doc(db, "test", sclass, "quarter", quarter, "test", test_name), {
          qno: intqno,
          order: order,
          test: test_name,
        })
          .then(() => {
            setorder("");
            setdoclength("");
            setadd("");
            setquestion("");
            setquestionnumber("");
            setanswer("");
            setoption1("");
            setoption2("");
            setoption3("");
            setoption4("");
            alert("Test Added");
            // setloader(false);
          })
          .catch((error) => {
            alert("error.message");
            // setloader(false);
          });
      });
    });
  };
  const deletetest = () => {
    if (sclass === "" || quarter === "" || test_name === "") {
      alert("Complete the fields");
    } else {
      const deleteref = doc(
        db,
        "test",
        sclass,
        "quarter",
        quarter,
        "test",
        test_name
      );
      deleteDoc(deleteref)
        .then(function () {
          alert("Data Deleted");
          window.location.reload();
        })
        .catch((error) => {
          alert("Error Try Again");
        });
    }
  };
  return (
    <div>
      <form className="form">
        <div className={classes.root}>
          <FormControl required className={classes.formControl}>
            <InputLabel id="demo-simple-select-required-label">
              Class
            </InputLabel>
            <Select
              labelId="demo-simple-select-required-label"
              id="demo-simple-select-required"
              value={sclass}
              onChange={(e) => setsclass(e.target.value)}
              className={classes.selectEmpty}
            >
              <MenuItem value="">
                <em>Classes</em>
              </MenuItem>
              <MenuItem value={"english-6"}>english-6</MenuItem>
              <MenuItem value={"english-7"}>english-7</MenuItem>
              <MenuItem value={"english-8"}>english-8</MenuItem>
              <MenuItem value={"english-9"}>english-9</MenuItem>
              <MenuItem value={"english-10"}>english-10</MenuItem>
              <MenuItem value={"malayalam-6"}>malayalam-6</MenuItem>
              <MenuItem value={"malayalam-7"}>malayalam-7</MenuItem>
              <MenuItem value={"malayalam-8"}>malayalam-8</MenuItem>
              <MenuItem value={"malayalam-9"}>malayalam-9</MenuItem>
              <MenuItem value={"malayalam-10"}>malayalam-10</MenuItem>
            </Select>
            <FormHelperText>Please Select class</FormHelperText>
          </FormControl>
          <FormControl required className={classes.formControl}>
            <InputLabel id="demo-simple-select-required-label">
              Quarter
            </InputLabel>
            <Select
              labelId="demo-simple-select-required-label"
              id="demo-simple-select-required"
              value={quarter}
              onChange={(e) => setquarter(e.target.value)}
              className={classes.selectEmpty}
            >
              <MenuItem value="">
                <em>Quarter</em>
              </MenuItem>
              <MenuItem value={"first"}>First</MenuItem>
              <MenuItem value={"second"}>Second</MenuItem>
              <MenuItem value={"third"}>Third</MenuItem>
            </Select>
            <FormHelperText>Please Select Quarter</FormHelperText>
          </FormControl>
          <FormControl required className={classes.formControl}>
            <div className={classes.text4}>
              <TextField
                id="outlined-name"
                label="Test-name"
                value={test_name}
                onChange={(e) => settest_name(e.target.value)}
                variant="outlined"
              />
            </div>
          </FormControl>
          <FormControl required className={classes.formControl}>
            <div className={classes.text4}>
              <TextField
                id="outlined-name"
                label="Order"
                value={order}
                onChange={(e) => setorder(e.target.value)}
                variant="outlined"
              />
            </div>
          </FormControl>
          <div>
            {add ? null : (
              <Button
                variant="contained"
                color="primary"
                size="large"
                onClick={() => handleaddcontrol()}
                className={classes.button}
              >
                Add Question
              </Button>
            )}
            <Button
              type="submit"
              variant="contained"
              color="primary"
              size="large"
              className={classes.button}
              onClick={() => history.push("/")}
            >
              Home
            </Button>
          </div>
        </div>
        {add ? (
          <div>
            <div>
              <FormControl required className={classes.formControl}>
                <div className={classes.text2}>
                  <TextField
                    id="outlined-name"
                    label="Type your question here......"
                    value={question}
                    onChange={(e) => setquestion(e.target.value)}
                    variant="outlined"
                  />
                </div>
              </FormControl>
            </div>
            <FormControl required className={classes.formControl}>
              <div className={classes.text3}>
                <TextField
                  id="outlined-name"
                  label="Option 1"
                  value={option1}
                  onChange={(e) => setoption1(e.target.value)}
                  variant="outlined"
                />
              </div>
            </FormControl>
            <FormControl required className={classes.formControl}>
              <div className={classes.text4}>
                <TextField
                  id="outlined-name"
                  label="Option 2"
                  value={option2}
                  onChange={(e) => setoption2(e.target.value)}
                  variant="outlined"
                />
              </div>
            </FormControl>
            <FormControl required className={classes.formControl}>
              <div className={classes.text3}>
                <TextField
                  id="outlined-name"
                  label="Option 3"
                  value={option3}
                  onChange={(e) => setoption3(e.target.value)}
                  variant="outlined"
                />
              </div>
            </FormControl>
            <FormControl required className={classes.formControl}>
              <div className={classes.text4}>
                <TextField
                  id="outlined-name"
                  label="Option 4"
                  value={option4}
                  onChange={(e) => setoption4(e.target.value)}
                  variant="outlined"
                />
              </div>
            </FormControl>
            <div>
              <FormControl required className={classes.formControl}>
                <InputLabel id="demo-simple-select-required-label">
                  Answer
                </InputLabel>
                <Select
                  labelId="demo-simple-select-required-label"
                  id="demo-simple-select-required"
                  value={answer}
                  onChange={(e) => setanswer(e.target.value)}
                  className={classes.selectEmpty}
                >
                  <MenuItem value="">
                    <em>Answer</em>
                  </MenuItem>
                  <MenuItem value={option1}>{option1}</MenuItem>
                  <MenuItem value={option2}>{option2}</MenuItem>
                  <MenuItem value={option3}>{option3}</MenuItem>
                  <MenuItem value={option4}>{option4}</MenuItem>
                </Select>
                <FormHelperText>Please Select Answer</FormHelperText>
              </FormControl>
              <Button
                onClick={() => validation()}
                variant="contained"
                color="primary"
                size="large"
                className={classes.button}
                startIcon={<SaveIcon />}
              >
                Save
              </Button>
            </div>
          </div>
        ) : null}
        <Button
          onClick={() => deletetest()}
          variant="contained"
          color="primary"
          size="large"
          className={classes.button}
          startIcon={<SaveIcon />}
        >
          Delete Test
        </Button>
      </form>
    </div>
  );
}
