import React from 'react'
import {  onSnapshot,doc,collection,deleteDoc} from "firebase/firestore";
import { db } from "./firebase"; 

export default function MentorRequest() {
 const[allDocs,SetAlldocs]=React.useState([]);
 const[user,setuser] =React.useState('')
    const startlistning=()=>{
  const response = collection(db, 'mentor')
     
       onSnapshot(response,(snapshot)=>{
       if(snapshot.docs.length){
         snapshot.docs.forEach((doc)=>{
           SetAlldocs((prev)=>{
             return[...prev,{data:doc.data(),id:doc.id}];
           });
         });
       }
     });
    }
    const deleteuser = () =>{
        if(user){
                const deleteref= doc(db,"mentor",user)
                deleteDoc(deleteref).then(function() {
                alert("Data Deleted")
                window.location.reload();
                }).catch((error) => {
                alert("Error Try Again")
                });
        }else{
            alert('Select Student to delete')

        }
        
    }
  

    return (
        <div>
            <button onClick={()=>startlistning()}>Refresh</button>
            <div>
                {
             allDocs.map((doc)=>{
                 return(
                        <>
                        <br/>
                        {/* {doc.data.name?null:<h1> No Mentor Requests Available</h1>} */}
                        Name : {doc.data.name}<br/>
                        Number : {doc.data.mobile}<br/>
                        registered number : {doc.data.registered_mob} &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; 
                        <button onClick={()=>{
                        deleteuser()     
                        }} >Delete</button> <br/>
                        medium : {doc.data.medium}<br/>
                        standard : {doc.data.standard}<br/>
                         <input type="radio" value={doc.id} id="male"
                            onChange={ (e)=>setuser(e.target.value)
                            } name="gender"
             />
                         <label for="male">Details Noted </label>  <br/>
                        ----------------------------------------------------------------------------------------------
                        </>
                 )
             })
            }
             </div>
             {allDocs.length===0?<div>
                 <h1> No Mentor Requests Available</h1>
             <h2>Press Refresh Button to Try Again</h2>
             </div>:null}
        </div>
    )
}
