import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormHelperText from "@material-ui/core/FormHelperText";
import Select from "@material-ui/core/Select";
import TextField from "@material-ui/core/TextField";
import { setDoc, doc, deleteDoc } from "firebase/firestore";
import { db } from "./firebase";
import { useHistory } from "react-router-dom";

import {
  getStorage,
  ref,
  getDownloadURL,
  uploadBytesResumable,
  deleteObject,
} from "firebase/storage";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 200,
    marginLeft: 100,
    marginTop: 20,
  },
  selectEmpty: {
    marginTop: theme.spacing(10),
  },
  button: {
    margin: theme.spacing(1),
    marginTop: 50,
  },
  text1: {
    "& .MuiTextField-root": {
      margin: theme.spacing(1),
      width: "40ch",
      marginTop: "5ch",
      marginLeft: "30ch",
    },
  },
  text2: {
    "& .MuiTextField-root": {
      margin: theme.spacing(0),
      width: "30ch",
      marginLeft: "10ch",
    },
  },
}));

export default function Questionpaper() {
  const history = useHistory();
  const classes = useStyles();
  const [sclass, setsclass] = React.useState("");
  const [subject, setsubject] = React.useState("");
  const [title, settitle] = React.useState("");
  const [probar, setprobar] = React.useState("*No file Selected");
  const [Url, setUrl] = React.useState("");

  const onChange = (e) => {
    const storage = getStorage();
    const imagesRef = ref(storage, "questionpaper/" + title + "/" + title);

    const file = e.target.files[0];
    const uploadTask = uploadBytesResumable(imagesRef, file);

    uploadTask.on("state_changed", (snapshot) => {
      var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;

      setprobar("Upload status : " + progress + "%");

      console.log("Uploaded a blob or file!");
      getDownloadURL(ref(storage, "questionpaper/" + title + "/" + title)).then(
        (url) => {
          setUrl(url);
          console.log(url);
        }
      );
    });
  };
  const validation = () => {
    if (subject === "" || sclass === "" || title === "") {
      alert("Complete the fields");
    } else {
      Upload();
    }
  };
  const Upload = () => {
    const datas = {
      pdfurl: Url,
      title: title,
    };
    setDoc(
      doc(db, "previous", sclass, "previous", subject, "papers", title),
      datas
    ).then(() => {
      setsclass("");
      setsubject("");
      settitle("");
      alert("Uploading Complete");
    });
  };
  const deletequestion = () => {
    if (subject === "" || sclass === "" || title === "") {
      alert("Complete the fields");
    } else {
      const storage = getStorage();
      const cloudref = ref(storage, "questionpaper/" + title + "/" + title);
      deleteObject(cloudref).then(() => {
        const deleteref = doc(
          db,
          "previous",
          sclass,
          "previous",
          subject,
          "papers",
          title
        );
        deleteDoc(deleteref).then(() => {
          alert("File Deleted");
          window.location.reload();
        });
      });
    }
  };
  return (
    <div>
      <form className="form" onSubmit={validation}>
        <div>
          <h1>Upload Class</h1>
        </div>
        <div className={classes.root}>
          <FormControl required className={classes.formControl}>
            <InputLabel id="demo-simple-select-required-label">
              Medium
            </InputLabel>
            <Select
              labelId="demo-simple-select-required-label"
              id="demo-simple-select-required"
              value={sclass}
              onChange={(e) => setsclass(e.target.value)}
              className={classes.selectEmpty}
            >
              <MenuItem value="">
                <em>Medium</em>
              </MenuItem>
              <MenuItem value={"malayalam"}>Malayalam</MenuItem>
              <MenuItem value={"english"}>English</MenuItem>
            </Select>
            <FormHelperText>Please Select Medium</FormHelperText>
          </FormControl>
          <FormControl required className={classes.formControl}>
            <InputLabel id="demo-simple-select-required-label">
              subject
            </InputLabel>
            <Select
              labelId="demo-simple-select-required-label"
              id="demo-simple-select-required"
              value={subject}
              onChange={(e) => setsubject(e.target.value)}
              className={classes.selectEmpty}
            >
              <MenuItem value="">
                <em>Subject</em>
              </MenuItem>
              <MenuItem value={"english"}>English</MenuItem>
              <MenuItem value={"biology"}>Biology</MenuItem>
              <MenuItem value={"social science"}>Social Science</MenuItem>
              <MenuItem value={"maths"}>Mathematics</MenuItem>
              <MenuItem value={"hindi"}>Hindi</MenuItem>
              <MenuItem value={"malayalam"}>Malayalam</MenuItem>
              <MenuItem value={"malayalam-2"}>Malayalam-II</MenuItem>
              <MenuItem value={"it (information technology)"}>IT</MenuItem>
              <MenuItem value={"chemistry"}>chemistry</MenuItem>
              <MenuItem value={"physics"}>physics</MenuItem>
            </Select>
            <FormHelperText>Please Select Subject</FormHelperText>
          </FormControl>
          <FormControl required className={classes.formControl}>
            <div className={classes.text2}>
              <TextField
                id="outlined-name"
                label="Title"
                value={title}
                onChange={(e) => settitle(e.target.value)}
                variant="outlined"
              />
            </div>
          </FormControl>
          {title ? (
            <>
              <div>{probar}</div>
              <input
                type="file"
                className={classes.button}
                onChange={onChange}
              />
            </>
          ) : null}
        </div>
        <Button
          variant="contained"
          color="primary"
          size="large"
          className={classes.button}
          onClick={() => validation()}
        >
          Upload File
        </Button>
        <Button
          variant="contained"
          color="primary"
          size="large"
          className={classes.button}
          onClick={() => deletequestion()}
        >
          Delete
        </Button>
      </form>
      <Button
        variant="contained"
        color="primary"
        size="large"
        className={classes.button}
        onClick={() => history.push("/")}
      >
        Home
      </Button>
    </div>
  );
}
