import React from "react";
import { onSnapshot, doc, collection } from "firebase/firestore";
import { db } from "./firebase";

export default function ViewallStudents() {
  const [allDocs, SetAlldocs] = React.useState([]);
  const startlistning = () => {
    const response = collection(db, "students-free");

    onSnapshot(response, (snapshot) => {
      if (snapshot.docs.length) {
        snapshot.docs.forEach((doc) => {
          SetAlldocs((prev) => {
            return [...prev, { data: doc.data(), id: doc.id }];
          });
        });
      }
    });
  };
  return (
    <div>
      <button onClick={() => startlistning()}>Refresh</button>
      <div>
        {allDocs.map((doc) => {
          return (
            <>
              <br />
              Name : {doc.data.name}
              <br />
              Number : {doc.data.mobile}
              <br />
              medium : {doc.data.medium}
              <br />
              standard : {doc.data.standard}
              <br />
              ---------------------------------------------------------------------------------------------------------------------------------------------------
            </>
          );
        })}
      </div>
    </div>
  );
}
