import React  from 'react'
import { doc,onSnapshot,deleteDoc } from "firebase/firestore";
import { db } from "./firebase"; 

const EditLessons =()=> {
   const[questionnumber,setquestionnumber]=React.useState('');
   onSnapshot (doc(db,"class","english-6","subjects","english","lessons","lesson-1"),(doc) => {
            const qno=doc.get("qno")
            setquestionnumber(qno)
   });
  const handledelete =(e)=>{
const loopconst =  parseInt(questionnumber);
     for (let i = 100; i < loopconst; i++) {
       
       deleteDoc(doc(db,"class","english-6","subjects","english","lessons",'lesson-1','test',i.toString()),)
     }
 }

    return (
      <div>
        {questionnumber}
      <button
      onClick={handledelete}>Delete</button>
      </div>
    )
  
}
export default EditLessons