import  React,{useState} from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Modal} from 'react-bootstrap'
import 'react-toastify/dist/ReactToastify.css';
import {ToastContainer} from 'react-toastify'
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import { makeStyles } from '@material-ui/core/styles';
import FormHelperText from '@material-ui/core/FormHelperText';
import Select from '@material-ui/core/Select';
import { useHistory} from 'react-router-dom';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import { onSnapshot ,collection,updateDoc,doc,deleteDoc} from "firebase/firestore";
import { db } from "./firebase"; 


const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 200,
    marginLeft:100,
    marginTop:20, 
  },
  selectEmpty: {
    marginTop: theme.spacing(10),
  },
  button: {
    margin: theme.spacing(1),
    marginTop:50,
    marginLeft:100
  
  },
  text1: {
    '& .MuiTextField-root': {
      margin: theme.spacing(1),
      width: '30ch',
     marginTop:'2ch',
     marginLeft:'-1ch',
  
    },
  },
  text2: {
    '& .MuiTextField-root': {
      margin: theme.spacing(0),
     width: '50ch',
     marginLeft:'10ch',
     padding:'2ch'
          },
  },
   text3: {
    '& .MuiTextField-root': {
      margin: theme.spacing(0),
     width: '40ch',
     marginLeft:'20ch',
          },
  },
   text4: {
    '& .MuiTextField-root': {
      margin: theme.spacing(0),
     width: '40ch',
     marginLeft:'5ch',
          },

        
  },
}));
const useStyles1 = makeStyles((theme) => ({

  data:{
    marginLeft:'10ch',
    marginTop:'5ch',
    
    

  },
 


}));


function UpdateTest() {
   
  
  const classes = useStyles();
  const datas = useStyles1();
  const[allDocs,SetAlldocs]=useState([]);
  const [modalShow, setModalShow] = React.useState(false);
  const [subject, setsubject] = React.useState('');
  const [sclass, setsclass] = React.useState('');
  const[lesson,setlesson]=React.useState('');
  const history=useHistory();
  const[sub_subject,setsub_subject]=React.useState('');
     const[questionnumber,setquestionnumber]=React.useState('');

  
 
   const[updata,setupdata]=useState({
     data:{
      name:'',
       title:''
       
     },
     id:''
   })
   

   const handlesearch=(e)=>{
     
     if (sclass===""||subject===""||sub_subject===""||lesson==="") {
       alert("Fill All Fields")
     }else{
     if(sub_subject==="nil"){
        onSnapshot (doc(db,"class",sclass,"subjects",subject,"lessons",lesson),(doc) => {
            const qno=doc.get("qno")
            setquestionnumber(qno)
   });
  
 const response = collection(db, 'class',sclass,'subjects',subject,'lessons',lesson,'test')
     
  onSnapshot(response,(snapshot)=>{
       if(snapshot.docs.length){
         snapshot.docs.forEach((doc)=>{
           SetAlldocs((prev)=>{
             return[...prev,{data:doc.data(),id:doc.id}];
           });
         });
       }
     });   
     }else{
        onSnapshot (doc(db,"class",sclass,"subjects",subject,subject,sub_subject,"lessons",lesson),(doc) => {
            const qno=doc.get("qno")
            setquestionnumber(qno)
        });
        const response = collection(db,"class",sclass,"subjects",subject,subject,sub_subject,"lessons",lesson,'test')
         onSnapshot(response,(snapshot)=>{
       if(snapshot.docs.length){
         snapshot.docs.forEach((doc)=>{
           SetAlldocs((prev)=>{
             return[...prev,{data:doc.data(),id:doc.id}];
           });
         });
       }
     });   
     }
  
  } 
 }
 const handledelete=(e)=>{
if (sclass===""||subject===""||sub_subject===""||lesson==="") {
       alert("Fill All Fields")
     }else{
     if(sub_subject==="nil"){
       const loopconst =  parseInt(questionnumber);
     for (let i = 100; i < loopconst; i++) {
       
       deleteDoc(doc(db, 'class',sclass,'subjects',subject,'lessons',lesson,'test',i.toString()),)
     }


//  deleteDoc(collection(db, 'Class',sclass,'subjects',subject,'lessons',lesson,'test'),
        
      //  ).then(function() {
       updateDoc(doc(db,"class",sclass,"subjects",subject,"lessons",lesson),{
      qno:"100"
    }).then(()=>{
              alert("Data Deleted")
              window.location.reload();
                        
            }).catch((error) => {
            alert("Error Try Again")
            });
     // })

  
     }else{

      const loopconst =  parseInt(questionnumber);
     for (let i = 100; i < loopconst; i++) {
       
       deleteDoc(doc(db,"class",sclass,"subjects",subject,subject,sub_subject,"lessons",lesson,'test',i.toString()),)
     }
      //   const response = collection(db,"Class",sclass,"subjects",subject,subject,sub_subject,"lessons",lesson,'test')
      // deleteDoc(response).then(function() {
       updateDoc(doc(db,"class",sclass,"subjects",subject,subject,sub_subject,"lessons",lesson),{
      qno:"100"
    }).then(()=>{
              alert("Data Deleted")
              window.location.reload();
                        
            }).catch((error) => {
            alert("Error Try Again")
            });
//})
           
     }
  
  } 
  }
    
     
      function MyVerticallyCenteredModal(props) {
        const {daata}=props
        const[value,setvalue]=useState({
          question:daata.data.question, 
          a:daata.data.a,
          b:daata.data.b,
          c:daata.data.c,
          d:daata.data.d,
          answer:daata.data.answer,
          qno:daata.data.qno
 
        })
        const handleChange=(question)=>(e)=>{
          e.preventDefault();
          setvalue({...value,[question]:e.target.value})
 }
 const handleclose = () => setModalShow(false);
 const{question,a,b,c,d,qno,answer}=value;
 const Updatedoc=(e)=>{
   e.preventDefault()


 updateDoc(doc(db,'class',sclass,'subjects',subject,'lessons',lesson,'test', daata.id),{
    question: question,
    a:a,
    b:b,
    c:c,
    d:d,
    answer:answer,
    qno:qno
   }).then(()=>{
    setupdata({
      data:{
      question:'',
       a:'',
       b:'',
       c:'',
       d:'',
       qno:''
     },
     id:''
      
    })
     alert("upddated succesfully")
    
     window.location.reload()
   }).catch(()=>{
     alert("something went wrong")
   })
};
  return (
          <Modal
            {...props}
            size="lg"
            aria-labelledby="contained-modal-title-vcenter"   
          >
            <Modal.Header closeButton onClick={handleclose}>
              <Modal.Title id="contained-modal-title-vcenter">
                QUESTION
              </Modal.Title>
            </Modal.Header>
            <Modal.Body>
             <form onSubmit={Updatedoc}>
              <div  className={classes.text2}>
             <TextField
          id="outlined-name"
          label="QUESTION"
          value={question}
        onChange={handleChange("question")}
          variant="outlined"
        /> </div>
           <div  className={classes.text2}>
             <TextField
          id="outlined-name"
          label="Option A"
          value={a}
        onChange={handleChange("a")}
          variant="outlined"
        /> </div>
          <div  className={classes.text2}>
             <TextField
          id="outlined-name"
          label="Option B"
          value={b}
        onChange={handleChange("b")}
          variant="outlined"
        /> </div>
          <div  className={classes.text2}>
             <TextField
          id="outlined-name"
          label="Option C"
          value={c}
        onChange={handleChange("c")}
          variant="outlined"
        /> </div>
          <div   className={classes.text2}>
             <TextField
          id="outlined-name"
          label="Option D"
          value={d}
        onChange={handleChange("d")}
          variant="outlined"
        /> </div>
          <div   className={classes.text2}>
             <TextField
          id="outlined-name"
          label="Answer"
          value={answer}
        onChange={handleChange("answer")}
          variant="outlined"
        /> </div>
        
             
             <button  className={classes.button}>UPDATE QUESTION</button>
             </form>
            </Modal.Body>
            <Modal.Footer>
              <Button onClick={handleclose}>Close</Button>
            </Modal.Footer>
          </Modal>
        );
      }
    
     
    return (  
<div>
  <div>
<form className="form" >
     
    <div className={classes.root}>
     
      <FormControl required className={classes.formControl}>
        <InputLabel id="demo-simple-select-required-label">Class</InputLabel>
        <Select
          labelId="demo-simple-select-required-label"
          id="demo-simple-select-required"
          value={sclass}
          onChange={e=>setsclass(e.target.value)}
          className={classes.selectEmpty}
        >
          <MenuItem value="">
            <em>Classes</em>
          </MenuItem>
          <MenuItem value={"english-6"}>english-6</MenuItem>
          <MenuItem value={"english-7"}>english-7</MenuItem>
          <MenuItem value={"english-8" }>english-8</MenuItem>
          <MenuItem value={"english-9"}>english-9</MenuItem>
          <MenuItem value={"english-10"}>english-10</MenuItem>
          <MenuItem value={"malayalam-6"}>malayalam-6</MenuItem>
          <MenuItem value={"malayalam-7"}>malayalam-7</MenuItem>
          <MenuItem value={"malayalam-8" }>malayalam-8</MenuItem>
          <MenuItem value={"malayalam-9"}>malayalam-9</MenuItem>
          <MenuItem value={"malayalam-10"}>malayalam-10</MenuItem>
        </Select>
        <FormHelperText>Please Select class</FormHelperText>
        </FormControl>
        <FormControl required className={classes.formControl}>
        <InputLabel id="demo-simple-select-required-label">subject</InputLabel>
        <Select
          labelId="demo-simple-select-required-label"
          id="demo-simple-select-required"
          value={subject}
          onChange={(e)=>setsubject(e.target.value)}
          className={classes.selectEmpty}
        >
          <MenuItem value="">
            <em>Subject</em>
          </MenuItem>
          <MenuItem value={"english"}>English</MenuItem>
          <MenuItem value={"science"}>Science</MenuItem>
          <MenuItem value={"social science"}>Social Science</MenuItem>
          <MenuItem value={"maths"}>Mathematics</MenuItem>
          <MenuItem value={"hindi"}>Hindi</MenuItem>
        </Select>
        <FormHelperText>Please Select Subject</FormHelperText>
      </FormControl>
      <FormControl required className={classes.formControl}>
        <InputLabel id="demo-simple-select-required-label">Division</InputLabel>
        <Select
          labelId="demo-simple-select-required-label"
id="demo-simple-select-required"
          value={sub_subject}
          onChange={(e)=>setsub_subject(e.target.value)}
          className={classes.selectEmpty}
        >
          <MenuItem value="">
            <em>Subject</em>
          </MenuItem>
           <MenuItem value={"nil"}>Nil</MenuItem>
          <MenuItem value={"biology"}>Biology</MenuItem>
          <MenuItem value={"chemistry"}>Chemistry</MenuItem>
          <MenuItem value={"physics"}>Physics</MenuItem>
          <MenuItem value={"history"}>History</MenuItem>
          <MenuItem value={"geography"}>Geography</MenuItem>
          <MenuItem value={"civics"}>Civics</MenuItem>
<MenuItem value={"constitution"}>Constitution</MenuItem>
          <MenuItem value={"economics"}>Economics</MenuItem>
         
         
        </Select>
        <FormHelperText>Please Select Subject</FormHelperText>
      </FormControl>
     
      
      <FormControl required className={classes.formControl}>
        <InputLabel id="demo-simple-select-required-label">Lesson</InputLabel>
        <Select
          labelId="demo-simple-select-required-label"
          id="demo-simple-select-required"
          value={lesson}
          onChange={(e)=>setlesson(e.target.value)}
          className={classes.selectEmpty}
        >
          <MenuItem value="">
            <em>Lessons</em>
          </MenuItem>
          <MenuItem value={"lesson-1"}>Lesson 1</MenuItem>
          <MenuItem value={"lesson-2"}>Lesson 2</MenuItem>
          <MenuItem value={"lesson-3"}>Lesson 3</MenuItem>
          <MenuItem value={"lesson-4"}>Lesson 4</MenuItem>
          <MenuItem value={"lesson-5"}>Lesson 5</MenuItem>
          <MenuItem value={"lesson-6"}>Lesson 6</MenuItem>
          <MenuItem value={"lesson-7"}>Lesson 7</MenuItem>
          <MenuItem value={"lesson-8"}>Lesson 8</MenuItem>
          <MenuItem value={"lesson-9"}>Lesson 9</MenuItem>
          <MenuItem value={"lesson-10"}>Lesson 10</MenuItem>
          <MenuItem value={"lesson-11"}>Lesson 11</MenuItem>
          <MenuItem value={"lesson-12"}>Lesson 12</MenuItem>
          <MenuItem value={"lesson-13"}>Lesson 13</MenuItem>
          <MenuItem value={"lesson-14"}>Lesson 14</MenuItem>
          <MenuItem value={"lesson-15"}>Lesson 15</MenuItem>
          <MenuItem value={"lesson-16"}>Lesson 16</MenuItem>
          <MenuItem value={"lesson-17"}>Lesson 17</MenuItem>
          <MenuItem value={"lesson-18"}>Lesson 18</MenuItem>

         
        </Select>
        <FormHelperText>Please Select Lesson</FormHelperText>
      </FormControl>
      <div>
        <Button
           variant="contained"
            color="primary"
            size="large"
            
            onClick={()=>handlesearch() }
            className={classes.button}         
      >
       Search
      </Button>
        <Button
    type="submit"
        variant="contained"
        color="primary"
        size="large"
        className={classes.button}
        onClick={()=> history.push("/")}
      >
        Home
      </Button>
   
      <Button
    
        variant="contained"
        color="primary"
        size="large"
        className={classes.button}
        onClick={()=> handledelete()}
      >
        Delete Test
      </Button>
       
            </div>
            
            </div>
            </form>

  </div>
<ToastContainer/>
    <div className={datas.data}>
      {
        allDocs.map((doc)=>{
          
          return(
              <div >
              <h2><b><u>Question No:{doc.data.qno}</u></b></h2>
              <h3> Question:{doc.data.question}</h3>
              <h3>Option A:{doc.data.a}</h3>
              <h3>Option B:{doc.data.b}</h3>
              <h3>Option C:{doc.data.c}</h3>
              <h3>Option D:{doc.data.d}</h3>
         
              <h2>Answer:{doc.data.answer}</h2>
              
              <button onClick={()=>{setModalShow(true)
              setupdata(doc)}}>UPDATE</button>
              
              </div>
               )
        })
      }
    </div>
    <MyVerticallyCenteredModal
        show={modalShow}
        onHide={() => setModalShow(true)}
        daata={updata}
      />
   </div>
      
  )
 }
            
export default UpdateTest;