import React from 'react'
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import Select from '@material-ui/core/Select';
import { useHistory} from 'react-router-dom';
import SaveIcon from '@material-ui/icons/Save';
import { db } from "./firebase"; 
import {  setDoc,doc,onSnapshot,updateDoc} from "firebase/firestore"; 
import TextField from '@material-ui/core/TextField';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 200,
    marginLeft:100,
    marginTop:20, 
  },
  selectEmpty: {
    marginTop: theme.spacing(10),
  },
  button: {
    margin: theme.spacing(1),
    marginTop:50,
    marginLeft:100
  
  },
  text1: {
    '& .MuiTextField-root': {
      margin: theme.spacing(1),
      width: '30ch',
     marginTop:'2ch',
     marginLeft:'-1ch',
  
    },
  },
  text2: {
    '& .MuiTextField-root': {
      margin: theme.spacing(0),
     width: '120ch',
     marginLeft:'10ch',
          },
  },
   text3: {
    '& .MuiTextField-root': {
      margin: theme.spacing(0),
     width: '40ch',
     marginLeft:'20ch',
          },
  },
   text4: {
    '& .MuiTextField-root': {
      margin: theme.spacing(0),
     width: '40ch',
     marginLeft:'5ch',
          },
  },
}));
//***************CSS Finished********************** */



//*******************Function Start****************** */

const UploadTest=()=> {
  const history=useHistory();
  const classes = useStyles();
  const [subject, setsubject] = React.useState('');
  const [sclass, setsclass] = React.useState('');
  const[lesson,setlesson]=React.useState('');
  const[sub_subject,setsub_subject]=React.useState('');
  const[option1,setoption1]=React.useState('');
  const[option2,setoption2]=React.useState('');
  const[option3,setoption3]=React.useState('');
  const[option4,setoption4]=React.useState('');
  const[question,setquestion]=React.useState('');
  const[answer,setanswer]=React.useState('');
  const[add,setadd]=React.useState('');
   const[questionnumber,setquestionnumber]=React.useState('');
   //*******************************Const Decleration Finished**************************/





//****************************************Validation Starts********************************************* */
const validation=(e)=>{
 //e.preventDefault();
 
if (question===""||answer===""||option1===""||option2===""||option3===""||option4===""||sclass===""||subject===""||lesson ==="") {
    
   alert("Enter Values")
    
    
}else if (questionnumber==="") {
  alert("No Data Found")
}
else{
 handlesubmit(); 
}
 }

//*****************************************Validation Finished************************************************* */








//**********************************************Upload Starts***************************************************** */
const handlesubmit=(e)=>{
    var intqno = parseInt(questionnumber);
    intqno=intqno+ 1
    if(sub_subject==="nil"){
   
    const datas = {
      question:question,
      a:option1,
      b:option2,
      c:option3,
      d:option4,
      answer:answer,
      qno:questionnumber 
    }   
     setDoc(doc(db,"class",sclass,"subjects",subject,"lessons",lesson,"test",questionnumber.toString()),
     datas
    ).then(()=>{
      
     updateDoc(doc(db,"class",sclass,"subjects",subject,"lessons",lesson),{
     qno:intqno
    }).then(()=>{
        setadd("")
        setquestion("")
        setquestionnumber("")
        setanswer("")
        setoption1("")
        setoption2("")
        setoption3("")
        setoption4("")
       alert("Test Added");
     
    
      // setloader(false);
    })
    .catch((error)=>{
      alert("error.message");
      // setloader(false);
    });
    
      
    })
   
  }else {
     
    const datas = {
      question:question,
      a:option1,
      b:option2,
      c:option3,
      d:option4,
      answer:answer,
      qno:questionnumber   
    }
    updateDoc(doc(db,"class",sclass,"subjects",subject,subject,sub_subject,"lessons",lesson),{
      qno:intqno
    }).then(()=>{
       
      setDoc(doc(db,"class",sclass,"subjects",subject,subject,sub_subject,"lessons",lesson,"test",questionnumber.toString()),
      datas
    )
  
      // setloader(false);
    })
    .then(()=>{
      setadd("")
        setquestion("")
        setquestionnumber("")
        setanswer("")
        setoption1("")
        setoption2("")
        setoption3("")
        setoption4("")

      alert("Test Added");
      // setloader(false);
    })
    .catch((error)=>{
      alert("error.message");
      // setloader(false);
    });

  }
}

//***************************************Adding Test**********************************************/
  const handleaddcontrol=(e)=>{
    if (sclass===""||subject===""||sub_subject===""||lesson==="") {
      alert("Select The Fields")
    }else{
      
      setadd("true")
      if (sub_subject==="nil") {
        
            onSnapshot (doc(db,"class",sclass,"subjects",subject,"lessons",lesson),(doc) => {
            const qno=doc.get("qno")
            setquestionnumber(qno)
           
            });
      }else{
          onSnapshot (doc(db,"class",sclass,"subjects",subject,subject,sub_subject,"lessons",lesson),(doc) => {
              const qno=doc.get("qno")
              setquestionnumber(qno)
              
          });
      }
    }
  }

//*********************************************Adding Finished**************************************/

        return (
        
            
    <form className="form" >
     
    <div className={classes.root}>
     
      <FormControl required className={classes.formControl}>
        <InputLabel id="demo-simple-select-required-label">Class</InputLabel>
        <Select
          labelId="demo-simple-select-required-label"
          id="demo-simple-select-required"
          value={sclass}
          onChange={e=>setsclass(e.target.value)}
          className={classes.selectEmpty}
        >
          <MenuItem value="">
            <em>Classes</em>
          </MenuItem>
          <MenuItem value={"english-6"}>english-6</MenuItem>
          <MenuItem value={"english-7"}>english-7</MenuItem>
          <MenuItem value={"english-8" }>english-8</MenuItem>
          <MenuItem value={"english-9"}>english-9</MenuItem>
          <MenuItem value={"english-10"}>english-10</MenuItem>
          <MenuItem value={"malayalam-6"}>malayalam-6</MenuItem>
          <MenuItem value={"malayalam-7"}>malayalam-7</MenuItem>
          <MenuItem value={"malayalam-8" }>malayalam-8</MenuItem>
          <MenuItem value={"malayalam-9"}>malayalam-9</MenuItem>
          <MenuItem value={"malayalam-10"}>malayalam-10</MenuItem>
        </Select>
        <FormHelperText>Please Select class</FormHelperText>
        </FormControl>
        <FormControl required className={classes.formControl}>
        <InputLabel id="demo-simple-select-required-label">subject</InputLabel>
        <Select
          labelId="demo-simple-select-required-label"
          id="demo-simple-select-required"
          value={subject}
          onChange={(e)=>setsubject(e.target.value)}
          className={classes.selectEmpty}
        >
          <MenuItem value="">
            <em>Subject</em>
          </MenuItem>
          <MenuItem value={"english"}>English</MenuItem>
          <MenuItem value={"science"}>Science</MenuItem>
          <MenuItem value={"social science"}>Social Science</MenuItem>
          <MenuItem value={"maths"}>Mathematics</MenuItem>
          <MenuItem value={"hindi"}>Hindi</MenuItem>
        </Select>
        <FormHelperText>Please Select Subject</FormHelperText>
      </FormControl>
      <FormControl required className={classes.formControl}>
        <InputLabel id="demo-simple-select-required-label">Division</InputLabel>
        <Select
          labelId="demo-simple-select-required-label"
          id="demo-simple-select-required"
          value={sub_subject}
          onChange={(e)=>setsub_subject(e.target.value)}
          className={classes.selectEmpty}
        >
          <MenuItem value="">
            <em>Subject</em>
          </MenuItem>
           <MenuItem value={"nil"}>Nil</MenuItem>
          <MenuItem value={"biology"}>Biology</MenuItem>
          <MenuItem value={"chemistry"}>Chemistry</MenuItem>
          <MenuItem value={"physics"}>Physics</MenuItem>
          <MenuItem value={"history"}>History</MenuItem>
          <MenuItem value={"geography"}>Geography</MenuItem>
          <MenuItem value={"civics"}>Civics</MenuItem>
          <MenuItem value={"constitution"}>Constitution</MenuItem>
          <MenuItem value={"economics"}>Economics</MenuItem>
         
         
        </Select>
        <FormHelperText>Please Select Subject</FormHelperText>
      </FormControl>
     
      
      <FormControl required className={classes.formControl}>
        <InputLabel id="demo-simple-select-required-label">Lesson</InputLabel>
        <Select
          labelId="demo-simple-select-required-label"
          id="demo-simple-select-required"
          value={lesson}
          onChange={(e)=>setlesson(e.target.value)}
          className={classes.selectEmpty}
        >
          <MenuItem value="">
            <em>Lessons</em>
          </MenuItem>
          <MenuItem value={"lesson-1"}>Lesson 1</MenuItem>
          <MenuItem value={"lesson-2"}>Lesson 2</MenuItem>
          <MenuItem value={"lesson-3"}>Lesson 3</MenuItem>
          <MenuItem value={"lesson-4"}>Lesson 4</MenuItem>
          <MenuItem value={"lesson-5"}>Lesson 5</MenuItem>
          <MenuItem value={"lesson-6"}>Lesson 6</MenuItem>
          <MenuItem value={"lesson-7"}>Lesson 7</MenuItem>
          <MenuItem value={"lesson-8"}>Lesson 8</MenuItem>
          <MenuItem value={"lesson-9"}>Lesson 9</MenuItem>
          <MenuItem value={"lesson-10"}>Lesson 10</MenuItem>
          <MenuItem value={"lesson-11"}>Lesson 11</MenuItem>
          <MenuItem value={"lesson-12"}>Lesson 12</MenuItem>
          <MenuItem value={"lesson-13"}>Lesson 13</MenuItem>
          <MenuItem value={"lesson-14"}>Lesson 14</MenuItem>
          <MenuItem value={"lesson-15"}>Lesson 15</MenuItem>
          <MenuItem value={"lesson-16"}>Lesson 16</MenuItem>
          <MenuItem value={"lesson-17"}>Lesson 17</MenuItem>
          <MenuItem value={"lesson-18"}>Lesson 18</MenuItem>

         
        </Select>
        <FormHelperText>Please Select Lesson</FormHelperText>
      </FormControl>
      <div>
        <Button
           variant="contained"
            color="primary"
            size="large"
            
            onClick={()=>handleaddcontrol() }
            className={classes.button}
            
      >
        Add Question
      </Button>
        <Button
    type="submit"
        variant="contained"
        color="primary"
        size="large"
        className={classes.button}
        onClick={()=> history.push("/")}
       
      >
        Home
      </Button>
       
            </div>
              
                  <div>
                    { add ? (
                      <div>
      <div>
           <FormControl required className={classes.formControl}>
      <div className={classes.text2}>
         <TextField
            id="outlined-name"
            label="Type your question here......"
            value={question}
            onChange={(e)=>setquestion(e.target.value)}
            variant="outlined"
        /> 
        
      </div>
      </FormControl>
      
      </div>
       <div>
           <FormControl required className={classes.formControl}>
      <div className={classes.text3}>
         <TextField
            id="outlined-name"
            label="Option 1"
            value={option1}
            onChange={(e)=>setoption1(e.target.value)}
            variant="outlined"
        /> 
        
      </div>
      </FormControl>
      <FormControl required className={classes.formControl}>
      <div className={classes.text4}>
         <TextField
            id="outlined-name"
            label="Option 2"
            value={option2}
            onChange={(e)=>setoption2(e.target.value)}
            variant="outlined"
        /> 
        
      </div>
      </FormControl>
      <FormControl required className={classes.formControl}>
      <div className={classes.text3}>
         <TextField
            id="outlined-name"
            label="Option 3"
            value={option3}
            onChange={(e)=>setoption3(e.target.value)}
            variant="outlined"
        /> 
        
      </div>
      </FormControl>
      <FormControl required className={classes.formControl}>
      <div className={classes.text4}>
         <TextField
            id="outlined-name"
            label="Option 4"
            value={option4}
            onChange={(e)=>setoption4(e.target.value)}
            variant="outlined"
        /> 
        
      </div>
      
      </FormControl>
      <div>
         
       <FormControl required className={classes.formControl}>
        <InputLabel id="demo-simple-select-required-label">Answer</InputLabel>
        <Select
          labelId="demo-simple-select-required-label"
          id="demo-simple-select-required"
          value={answer}
          onChange={(e)=>setanswer(e.target.value)}
          className={classes.selectEmpty}
        >
          <MenuItem value="">
            <em>Answer</em>
          </MenuItem>
          <MenuItem value={option1}>{option1}</MenuItem>
          <MenuItem value={option2}>{option2}</MenuItem>
          <MenuItem value={option3}>{option3}</MenuItem>
          <MenuItem value={option4}>{option4}</MenuItem>
         
         
         
        </Select>
        <FormHelperText>Please Select Answer</FormHelperText>
      </FormControl>
        <Button
            onClick={()=>validation()}
            variant="contained"
            color="primary"
            size="large"

            className={classes.button}
            startIcon={<SaveIcon />}
      >
        Save
      </Button>
      
      </div>
      </div>
      </div>
   ) : null }
      </div>
      </div>
      </form> 
     
        )   
}
export default UploadTest;
