import React from 'react'
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import Select from '@material-ui/core/Select';
import { useHistory} from 'react-router-dom';
import SaveIcon from '@material-ui/icons/Save';
import { db } from "./firebase"; 
import {  setDoc,doc,deleteDoc} from "firebase/firestore"; 
import TextField from '@material-ui/core/TextField';



const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 200,
    marginLeft:100,
    marginTop:20, 
  },
  selectEmpty: {
    marginTop: theme.spacing(10),
  },
  button: {
    margin: theme.spacing(1),
    marginTop:50,
  
  },
  button2: {
    margin: theme.spacing(1),
    marginTop:40,
    marginLeft:'30ch'
  },
  text1: {
    '& .MuiTextField-root': {
      margin: theme.spacing(1),
      width: '40ch',
     marginTop:'5ch',
     marginLeft:'30ch',
    
      
    },
  },
  text2: {
    '& .MuiTextField-root': {
      margin: theme.spacing(0),
     width: '30ch',
     marginLeft:'10ch',
          },
  },
   text3: {
    '& .MuiTextField-root': {
      margin: theme.spacing(0),
     width: '30ch',
     marginLeft:'0ch',
          },
  },
}));
const Softskills =()=> {
    const history=useHistory();
    const [subject,setsubject] = React.useState("")
    const [lesson,setlesson] = React.useState("")
    const[title,settitle] = React.useState("")
    const[videourl,setvideourl] = React.useState("")
    const classes = useStyles();
    const [ordernumber,setordernumber] = React.useState("")
    
  var number;

    const handlevalidation = (e) =>{
        e.preventDefault();
        if (subject===""||lesson===""||videourl===""||title==="") {
            alert("Fill All The Values")
        }else{
            number = parseInt(lesson);
           number= number+10
           
              setordernumber(number)
            uploadvalues();
        }
        

    }
    const uploadvalues =()=>{
      
console.log(number)

      console.log(ordernumber)
        const datas={
            lesson:"lesson-"+lesson,
            order:number,
            title:title,
            videourlone:videourl
        }
        setDoc(doc(db,"softskills",subject,"lessons","lesson-"+lesson),
        datas).then(()=>{
         alert("Data Added Successfully")
         window.location.reload();
        })
    }
    const handledelete =()=>{
      if (subject===""||lesson==="") {
        alert("Select Subject and Lesson")
      }else{
deleteDoc(doc(db, 'softskills',subject,'lessons',"lesson-"+lesson),).then(()=>{
  alert("Deleted Successfully")
}).catch((error) => {
            alert("Error Try Again")
            });
      }

    }


        return (
            <div>
                <form className="form" onSubmit={handlevalidation}>
      <div>
        <h1>Upload Soft Skill</h1>
      </div>
      
    <div className={classes.root}>
     
      <FormControl required className={classes.formControl}>
         
        <InputLabel id="demo-simple-select-required-label">Subject</InputLabel>
        <Select
          labelId="demo-simple-select-required-label"
          id="demo-simple-select-required"
          value={subject}
          onChange={(e)=>setsubject(e.target.value)}
          className={classes.selectEmpty}
        >
          <MenuItem value="">
            <em>Subject</em>
          </MenuItem>
          <MenuItem value={"drawing"}>Drawing</MenuItem>
          <MenuItem value={"singing"}>Singing</MenuItem>
          <MenuItem value={"dancing"}>Dancing</MenuItem>
          <MenuItem value={"handwriting"}>Hand writing</MenuItem>
         

         
        </Select>
        <FormHelperText>Please Select Subject</FormHelperText>
      </FormControl>
     
      <FormControl required className={classes.formControl}>
        <InputLabel id="demo-simple-select-required-label">Lesson</InputLabel>
        <Select
          labelId="demo-simple-select-required-label"
          id="demo-simple-select-required"
          value={lesson}
          onChange={(e)=>setlesson(e.target.value)}
          className={classes.selectEmpty}
        >
          <MenuItem value="">
            <em>Lesson</em> 
          </MenuItem>
          <MenuItem value={"1"}>Lesson 1</MenuItem>
          <MenuItem value={"2"}>Lesson 2</MenuItem>
          <MenuItem value={"3"}>Lesson 3</MenuItem>
          <MenuItem value={"4"}>Lesson 4</MenuItem>
          <MenuItem value={"5"}>Lesson 5</MenuItem>
          <MenuItem value={"6"}>Lesson 6</MenuItem>
          <MenuItem value={"7"}>Lesson 7</MenuItem>
          <MenuItem value={"8"}>Lesson 8</MenuItem>
          <MenuItem value={"9"}>Lesson 9</MenuItem>
          <MenuItem value={"10"}>Lesson 10</MenuItem>
          <MenuItem value={"11"}>Lesson 11</MenuItem>
          <MenuItem value={"12"}>Lesson 12</MenuItem>
          <MenuItem value={"13"}>Lesson 13</MenuItem>
          <MenuItem value={"14"}>Lesson 14</MenuItem>
          <MenuItem value={"15"}>Lesson 15</MenuItem>
          <MenuItem value={"16"}>Lesson 16</MenuItem>
          <MenuItem value={"17"}>Lesson 17</MenuItem>
          <MenuItem value={"18"}>Lesson 18</MenuItem>

         
        </Select>
        <FormHelperText>Please Select Lesson</FormHelperText>
      </FormControl>
          <Button
    
        variant="contained"
        color="primary"
        size="large"
        className={classes.button2}
        onClick={()=> handledelete()}
       
      >
       Delete Lesson
      </Button>
      <div>
      <FormControl required className={classes.formControl}>
        
      <div className={classes.text3}>
       
   <TextField
          id="outlined-name"
          label="Title" 
          value={title}
          onChange={(e)=>settitle(e.target.value)}
          variant="outlined"
        /></div>
           </FormControl>
           <FormControl required className={classes.formControl}>
         <div className={classes.text2}>
         <TextField
          id="outlined-name"
          label="Video URL"
          value={videourl}
        onChange={(e)=>setvideourl(e.target.value)}
          variant="outlined"
        /> 
       
      </div>
       </FormControl>
   </div>
    </div>
   
    <div className={classes.button}>
    <Button
    type="submit"
        variant="contained"
        color="primary"
        size="large"
        className={classes.button}
        startIcon={<SaveIcon />}
      >
        Save
      </Button>
      
    <Button
    type="submit"
        variant="contained"
        color="primary"
        size="large"
        className={classes.button}
        onClick={()=> history.push("/")}
       
      >
        Home
      </Button>
   
      </div>
        </form>
        <h5>Please select subject and lesson to delete lessons</h5>
      </div> 
         
        )
    
}
export default Softskills;
