import React from "react";
import Button from "@material-ui/core/Button";
import { useHistory } from "react-router-dom";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 200,
    marginLeft: 100,
    marginTop: 20,
  },
  selectEmpty: {
    marginTop: theme.spacing(10),
  },
  button: {
    margin: theme.spacing(1),
    marginTop: 50,
  },
  text1: {
    "& .MuiTextField-root": {
      margin: theme.spacing(1),
      width: "40ch",
      marginTop: "5ch",
      marginLeft: "30ch",
    },
  },
  text2: {
    "& .MuiTextField-root": {
      margin: theme.spacing(0),
      width: "30ch",
      marginLeft: "10ch",
    },
  },
}));

export default function Home() {
  const classes = useStyles();
  const history = useHistory();
  return (
    <div>
      <Button
        variant="contained"
        color="primary"
        size="large"
        className={classes.button}
        onClick={() => history.push("/uploadclass")}
      >
        Add Class
      </Button>
      <Button
        variant="contained"
        color="primary"
        size="large"
        className={classes.button}
        onClick={() => history.push("/editclass")}
      >
        Edit Class
      </Button>
      <Button
        variant="contained"
        color="primary"
        size="large"
        className={classes.button}
        onClick={() => history.push("/uploadtest")}
      >
        Add Test
      </Button>
      <Button
        variant="contained"
        color="primary"
        size="large"
        className={classes.button}
        onClick={() => history.push("/updatetest")}
      >
        Update Test
      </Button>
      <Button
        variant="contained"
        color="primary"
        size="large"
        className={classes.button}
        onClick={() => history.push("/uploadbanner")}
      >
        Add Banner
      </Button>
      <Button
        variant="contained"
        color="primary"
        size="large"
        className={classes.button}
        onClick={() => history.push("/softskillsupload")}
      >
        Soft Skills
      </Button>
      <Button
        variant="contained"
        color="primary"
        size="large"
        className={classes.button}
        onClick={() => history.push("/subscription")}
      >
        Extras
      </Button>
      <Button
        variant="contained"
        color="primary"
        size="large"
        className={classes.button}
        onClick={() => history.push("/demovideo")}
      >
        Demo Video
      </Button>
      <Button
        variant="contained"
        color="primary"
        size="large"
        className={classes.button}
        onClick={() => history.push("/generaltest")}
      >
        General Test
      </Button>
      <Button
        variant="contained"
        color="primary"
        size="large"
        className={classes.button}
        onClick={() => history.push("/mentorrequest")}
      >
        Mentor Request
      </Button>
      <Button
        variant="contained"
        color="primary"
        size="large"
        className={classes.button}
        onClick={() => history.push("/viewallstudents")}
      >
        View All Students
      </Button>
      <Button
        variant="contained"
        color="primary"
        size="large"
        className={classes.button}
        onClick={() => history.push("/questionpaper")}
      >
        Upload Question Paper
      </Button>
    </div>
  );
}
